}
.add-shop-title-bar{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0 auto;
    -ms-flex-align: start;
        align-items: flex-start;
}
.push-down{
    margin-top: 10px !important;
}
