*{
    -webkit-user-select: none !important;
       -moz-user-select: none !important;
        -ms-user-select: none !important;
            user-select: none !important;
}
.home-page{
    max-width: 100% !important;
    margin-bottom: 5%;
    overflow-x: hidden;
}
.title-bar{
    padding-top: 5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.title-bar img{
    margin-left: -105px;
    width: 250px;
    height: 250px;
}
.home-page-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 85%;
    margin: 0 auto;
    margin-top: 5%;
    -ms-flex-pack: center !important;
        justify-content: center !important;
}
.login-form{
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 0 10px;
    padding: 20px !important;
}
.push-down{
    margin-top: 10px !important;
}
@media only screen and (max-width: 1024px){
    .title-bar .title{
        font-size: 50px;
    }
    .title-bar .tag-line{
        font-size: 30px;
    }
}
@media only screen and (max-width: 768px){
    .title-bar .title{
        font-size: 50px;
    }
    .title-bar .tag-line{
        font-size: 20px;
    }
    .title-bar img{
        margin-left: -75px;
        width: 150px;
        height: 150px;
    }
    .home-page-content{
        -ms-flex-direction: column;
            flex-direction: column;
    }
}
@media only screen and (max-width: 650px){
    .title-bar .tag-line{
        display: none;
    }
    .title-bar img{
        margin-left: -25px;
    }
}


